.hero-wrapper {
    color: white;
    position: relative;
    padding-bottom: 2rem;
    z-index: 1;
}

.hero-container {
    justify-content: space-around;
    align-items: flex-end;

}

/*left side*/
.hero-left {
    gap: 3rem;
}

.hero-title {
    position: relative;
    z-index: 1;
}

.hero-title>h1 {
    font-weight: 600;
    font-size: 3.8rem;
    line-height: 4rem;
}

.orange-circle {
    height: 4rem;
    width: 4rem;
    background: var(--orange-gradient);
    border-radius: 999px;
    position: absolute;
    right: 35%;
    top: -10%;
    z-index: -1;
}

.search-bar {
    background-color: white;
    border: 3px solid rgba(120, 120, 120, 0.12);
    border-radius: 5px;
    padding: 0.5rem 1rem;
    justify-content: space-between;
    width: 100%;
}

.search-bar>input {
    border: none;
    outline: none;
}

.stat>:nth-child(1) {
    font-size: 2rem;
    padding: 10px;
}

.stat>:nth-child(1)>:last-child {
    color: orange;
    

}
.stat {
    text-align: center; /* Optional: Center align content */
    padding: 10px; /* Optional: Add padding around each stat */
  }

/*right side*/
.image-container {
    width: 30rem;
    height: 35rem;
    overflow: hidden;
    border-radius: 15rem 15rem 0 0;
    border: 8px solid rgba(255, 255, 255, 0.12);
}

.image-container>img {
    width: 100%;
    height: 100%;
}

@media(max-width : 640px){
    .hero-container{
        margin-top: 2rem;
    }
    .hero-title>h1{
        font-size: 2.5rem;
        line-height: 3rem;
    }
    .image-container{
        width: 95%;
        height: 25rem;
    }
    .stats{
        justify-content: center;
        gap: 1.5rem;
    }
    .hero-right{
        width: 100%;
    }
    .stat>:nth-child(1){
        font-size: 1.5rem;
    }
    .stat>:nth-child(2){
        font-size: 0.8rem;
    }
}


